<template lang="pug">
    .test__answer-item
        .test__number {{answerChar}}
        .test__answer-text
            textarea(@change='onChange()' v-model='answerItem' required ).test__answer
        vue-mathjax(:formula='answerItem' v-if='texTranslateVisible')
</template>
<script>
import {VueMathjax} from 'vue-mathjax'
export default {
    components: {
        'vue-mathjax': VueMathjax
    },
    props: {
        answerChar: String,
        texTranslateVisible: Boolean    
    },
    data() {
        return {
            answerItem: ''       
        }
    },
    watch: {
        newQuestion: function () {
            if(this.newMode) location.reload()
        }
    },
    methods: {
        onChange(){
            this.$emit('answerInput', {
                answerChar: this.answerChar,
                answer: this.answerItem
            })
        }
    }
}
</script>
<style lang="scss">
.mjx-chtml{ font-size: 100% !important; }
.test__answer-item {
    display: flex;
    width: 100%;
}
.test__number {
    width: 5%;
}
.test__answer {
    width: 100%;
    flex: 1;
    border-radius: 4px;
}
.test__answer-text {
    width: 44%;
}
</style>